/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import Helmet from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

function SEO({ description, lang, meta, title }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description

  const customStylesheets = [
    {
      href:
        "//fonts.googleapis.com/css?family=Saira+Extra+Condensed:500,700|Muli:400,400i,800,800i|Oswald:wght@300;400;700",
      rel: "stylesheet",
    },
    {
      href:
        "//cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css",
      rel: "stylesheet",
    },
    {
      href: "//cdnjs.cloudflare.com/ajax/libs/devicon/2.2/devicon.min.css",
      rel: "stylesheet",
    },
    {
      rel: "stylesheet",
      href:
        "//cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.min.css",
    },
  ]

  const customScripts = [
    {
      src: "//cdnjs.cloudflare.com/ajax/libs/jquery/3.4.1/jquery.slim.min.js",
      defer: "",
    },
    {
      src:
        "//cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.3.1/js/bootstrap.bundle.min.js",
      defer: "",
    },
  ]

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={title}
      titleTemplate={`%s | ${site.siteMetadata.title}`}
      link={customStylesheets}
      script={customScripts}
      meta={[
        {
          name: "viewport",
          content: "width=device-width, initial-scale=1, shrink-to-fit=no",
        },
        {
          name: `description`,
          content: metaDescription,
        },
        {
          property: `og:title`,
          content: title,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          name: `twitter:card`,
          content: `summary`,
        },
        {
          name: `twitter:creator`,
          content: site.siteMetadata.author,
        },
        {
          name: `twitter:title`,
          content: title,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
      ].concat(meta)}
    />
  )
}

SEO.defaultProps = {
  lang: `en`,
  meta: [],
  description: ``,
}

SEO.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
}

export default SEO
